@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
body,
html {
	margin: 0;
	padding: 0;
	font-size: 100%;
	box-sizing: border-box;
}

.mainWrapper {
	min-height: 100vh;
	width: 100%;
	background-color: #ffffff;

	.main {
		min-height: 100vh;
		width: 100%;
		background-color: #ffffff;
		position: relative;

		> img {
			height: 100vh;
			width: 100%;
			object-fit: cover;
			position: absolute;
			top: 0;
			z-index: 1;
			opacity: 0.75;
		}

		.heroSection {
			position: absolute;
			top: 0;
			height: 100vh;
			z-index: 2;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;

			> .leftHero {
				height: 100vh;
				width: 50%;
				padding: 0 0 0 7rem;
				display: flex;
				align-items: flex-start;
				justify-content: center;
				flex-direction: column;

				> h2 {
					font-family: 'Open Sans', sans-serif;
					line-height: 5rem;
					word-spacing: 0.75rem;
					font-size: 4rem;
					font-weight: 800;
					color: #252525;
					text-align: left;
					width: 100%;
				}

				p {
					font-family: 'Open Sans', sans-serif;
					font-size: 1.2rem;
					font-weight: 400;
					color: #000000;
					text-align: left;
					width: 100%;
					margin: 1rem 0 0 0;
				}

				> .buttonPredict {
					background-color: white;
					color: #000000;
					font-family: 'Open Sans', sans-serif;
					font-size: 1.2rem;
					font-weight: 600;
					padding: 1rem 2rem;
					margin: 2rem 0 0 0;
					border-radius: 10rem;
					cursor: pointer;
					transition: all 0.15s ease-in-out;

					&:hover {
						background-color: #000000;
						color: white;
					}
				}
			}

			> .rightHero {
				height: 100vh;
				width: 50%;
				display: flex;
				align-items: center;
				justify-content: center;

				> img {
					height: 70%;
					border-radius: 2rem;
					object-fit: contain;
				}

				@keyframes animate {
					0% {
						transform: scale(1);
					}
					50% {
						transform: scale(1.05);
					}
					100% {
						transform: scale(1);
					}
				}
			}
		}
	}
	> .sectionTwo {
		min-height: 20vh;
		width: 100%;
		background: black;
		padding: 3rem 0;

		> p {
			color: white;
			font-size: 2rem;
			font-weight: 700;
			text-align: center;
			margin: 2rem 0;
		}

		.sectionWrapper {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 2rem;

			> .section {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				border-right: 1px solid grey;
				width: 30%;
				height: 15rem;

				&:last-child {
					border: none;
				}

				> img {
					height: 3rem;
					width: 3rem;
				}

				h3 {
					color: white;
					font-size: 1.5rem;
					font-weight: 700;
					margin: 1rem 0 0 0;
				}

				> p {
					color: white;
					font-size: 1rem;
					font-weight: 400;
					text-align: center;
				}

				> a {
					color: cyan;
					font-size: 0.85rem;
					font-weight: 400;
					text-decoration: none;
					margin: 1rem 0 0 0;
				}
			}
		}
	}

	> .infoSection {
		height: fit-content;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 2% 5%;
		min-height: 75vh;

		> .left {
			width: 50%;

			> h3 {
				font-family: 'Open Sans', sans-serif;
				font-size: 2rem;
				font-weight: 700;
				color: #000000;
				text-align: left;
				margin: 2rem 0;
			}

			ul {
				list-style: none;
				margin: 2rem 0;
				padding: 0;

				li {
					font-family: 'Open Sans', sans-serif;
					font-size: 1.2rem;
					font-weight: 400;
					color: #000000;
					text-align: left;
					margin: 1rem 0;
				}
			}
		}

		> .right {
			width: 50%;
			display: flex;
			// align-items: center;
			justify-content: center;

			> img {
				height: 70%;
				border-radius: 2rem;
				object-fit: contain;
			}
		}
	}

	> .infoSectionTwo {
		height: fit-content;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: row-reverse;
		padding: 2% 5%;
		background-color: black;
		color: white;
		min-height: 75vh;

		> .left {
			width: 50%;

			> h3 {
				font-family: 'Open Sans', sans-serif;
				font-size: 2rem;
				font-weight: 700;
				text-align: left;
				margin: 2rem 0;
			}

			ul {
				list-style: none;
				margin: 2rem 0;
				padding: 0;

				li {
					font-family: 'Open Sans', sans-serif;
					font-size: 1.2rem;
					font-weight: 400;
					text-align: left;
					margin: 1rem 0;
					color: #d2d2d2;
				}
			}
		}

		> .right {
			width: 50%;
			display: flex;
			// align-items: center;
			// justify-content: center;

			.imageTwo {
				width: 50%;
				border-radius: 2rem;
				object-fit: cover;
			}

			> img {
				height: 70%;
				border-radius: 2rem;
				object-fit: contain;
			}
		}
	}
}

